<template>
    <div class="monitor-navigation">
        <bk-navigation
            :header-title="nav.id"
            :side-title="nav.title"
            :default-open="false"
            :need-menu="true"
            ext-cls="main-navigation"
            @toggle-click="handleToggle">
            <!--            导航头部图标-->
            <template slot="side-icon">
                <div class="monitor-logo" @click="goHome">
                    <img :src="'data:image/png;base64,' + nav.logo" height="32" width="32" :alt="'logo图片'">
                </div>
            </template>
            <!--            头部-->
            <template slot="header">
                <Header :nav="nav" :header="header" :user="user"></Header>
            </template>
            <!--            左侧导航栏-->
            <template slot="menu">
                <bk-navigation-menu
                    ref="menu"
                    @select="handleSelect"
                    :default-active="$route.meta.hasOwnProperty('fatherName') ? $route.meta.fatherName : $route.name"
                    :before-nav-change="beforeNavChange"
                    :toggle-active="nav.toggle">
                    <bk-navigation-menu-item
                        v-for="item in nav.list"
                        :key="item.name"
                        :has-child="item.children && !!item.children.length"
                        :icon="item.icon"
                        :id="item.id">
                        <span>{{item.name}}</span>
                        <div slot="child">
                            <bk-navigation-menu-item
                                v-for="child in item.children"
                                :key="child.name"
                                :id="child.id"
                                :url="child.url"
                                :default-active="child.active"
                                @click="handleNavItemClick(child)">
                                <span>{{child.name}}</span>
                            </bk-navigation-menu-item>
                        </div>
                    </bk-navigation-menu-item>
                </bk-navigation-menu>
            </template>
            <!--            容器-->
            <Container :nav-toggle="this.nav.toggle"></Container>
        </bk-navigation>
    </div>
</template>

<script>
    import Header from '@/components/base/header'
    import Container from '@/components/base/container'
    import {createAdminRouteConfig} from '../../router/frameRouter'

    export default {
        name: 'leftNavigation',
        components: {
            Header,
            Container
        },
        data () {
            return {
                nav: {
                    list: [],
                    name: '首页1',
                    id: 'HomeFirst',
                    toggle: false,
                    submenuActive: false,
                    title: '嘉为产品框架',
                    // 图片base64格式
                    logo: ''
                },
                header: {
                    selectList: [
                        {
                            name: '作业平台',
                            id: 1
                        },
                        {
                            name: '配置平台',
                            id: 2
                        }
                    ],
                    bizId: 1
                },
                user: {
                    name: 'Troytu'
                }
            }
        },
        created () {
            this.confirmUserIdentity()
        },
        methods: {
            confirmUserIdentity () {
                this.nav.list = createAdminRouteConfig()
                this.nav.name = this.$route.meta.title
            },
            handleSelect (id, item) {
                this.nav.id = id
                let a = 1
            },
            handleToggle (v) {
                this.nav.toggle = v
            },
            beforeNavChange (newId, oldId) {
                return true
            },
            handleNavItemClick (item) {
                if (this.$route.name !== item.id) {
                    this.nav.name = item.name
                    this.$router.push({
                        name: item.id
                    })
                }
            },
            goHome () {
                this.$router.push('/home')
                this.nav.name = this.$route.meta.title
            }
        }
    }
</script>

<style lang="scss" scoped>
    .bk-navigation {
        width: 100% !important;
    }

    /deep/ .bk-navigation-wrapper {
        height: 100vh !important;
        width: 100%;

        .navigation-container {
            max-width: calc(100% - 60px) !important;
        }

        .bk-navigation-title {
            padding: 13px 14px !important;
            flex-basis: 58px !important;

            .title-desc {
                font-size: 18px;
                font-weight: bold;
                color: #FFFFFF;
            }
        }
    }

    .monitor-logo img {
        display: flex;
        align-items: center;
    }

    .monitor-navigation-nav {
        width: 150px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        background: #FFFFFF;
        border: 1px solid #E2E2E2;
        -webkit-box-shadow: 0 3px 4px 0 rgba(64, 112, 203, 0.06);
        box-shadow: 0 3px 4px 0 rgba(64, 112, 203, 0.06);
        padding: 6px 0;
        margin: 0;
        color: #63656E;

        .nav-item {
            -webkit-box-flex: 0;
            -ms-flex: 0 0 32px;
            flex: 0 0 32px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            padding: 0 20px;
            list-style: none
        }

        .nav-item:hover {
            color: #3A84FF;
            cursor: pointer;
            background-color: #F0F1F5;
        }
    }
</style>



